<template>
<div style="color: #666;margin: 0px 100px 0px 100px">
    <h1 style="float: left;margin-right: 15px;color: #5392a6">知识专区:</h1>
    <div>
      <h1 class="article" @click="article1()">文章讨论区</h1>
      <h1 class="video" @click="video()">视频分享区</h1>
    </div>
  <hr style="margin-top: 5px">
  <div>
    <h2>视频名称:{{ videos.name }}</h2>
  </div>
  <div class='demo'>
    <video-player
                  class="vjs-big-play-centered"
                  ref="videoPlayer"
                  :playsinline="true"
                  :options="playerOptions">
    </video-player>
  </div>
</div>
</template>

<script>
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  name: "VideoDetail",
  components: {
    videoPlayer
  },
  data(){
    return{
      playerOptions: {
        playbackRates: [0.5, 1.0, 1.5, 2.0], // 可选的播放速度
        autoplay: false, // 如果为true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 是否视频一结束就重新开始。
        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [{
          type: "video/mp4", // 类型
          src: '' // url地址
        }],
        poster: '', // 封面地址
        notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true, // 当前时间和持续时间的分隔符
          durationDisplay: true, // 显示持续时间
          remainingTimeDisplay: true, // 是否显示剩余时间功能
          fullscreenToggle: true // 是否显示全屏按钮
        }
      },
      videos: [],
    }
  },
  created() {
    let id =  this.$route.query.id
    this.request("/videoFile/detail/" + id).then(res => {
      console.log(res.data)
      //赋值
      this.playerOptions.sources[0].src = res.data.url
      this.videos = res.data
    })
  },

   methods:{
     article1() {
       this.$router.push({path: "/front/Article"})
     },

     video() {
       this.$router.push({path: "/front/Video"})
     },
   }
}
</script>

<style scoped>
.demo{
  width: 95%;
  height: 95%;
  margin-bottom: 30px;
}

.article{
  float: left;
  margin-right: 10px
}

.article:hover{
  color: #5392a6;
  background: none;
  cursor: pointer;
}

.video:hover{
  color: #5392a6;
  background: none;
  cursor: pointer;
}
</style>